.alignment {
  &--center {
    text-align: center;
  }

  &--left {
    text-align: left;
  }

  &--right {
    text-align: right;
  }
}

.background-color {
  &--black {
    background-color: color("black");
  }

  &--white {
    background-color: color("white");
  }

  &--grey {
    background-color: color("grey");
  }

  &--grey-light {
    background-color: color("grey-light");
  }

  &--grey-opacity {
    background-color: color("grey-opacity");
  }

  &--yellow {
    background-color: color("yellow");
  }

  &--cyan {
    background-color: color("cyan");
  }

  &--purple {
    background-color: color("purple");
  }
}

.color {
  &--black {
    color: color("black");
  }

  &--white {
    color: color("white");
  }

  &--grey {
    color: color("grey");
  }

  &--grey-light {
    color: color("grey-light");
  }

  &--grey-opacity {
    color: color("grey-opacity");
  }

  &--yellow {
    color: color("yellow");
  }

  &--red {
    color: color("red");
  }
}

body {
  overflow: overlay;
}
*::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

*::-webkit-scrollbar-thumb {
  background: rgba(90, 90, 90);
}

*::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.2);
}
